import { useEffect } from 'react';
import { Button } from 'react-bootstrap';
import {motion} from 'framer-motion'
import React from 'react'

function Filter({ activeGenre, setActiveGenre, setFiltered, popular }) {

  useEffect(() =>
  {
    if(activeGenre === 0)
    {
      setFiltered(popular);
      return;
    }

    const filtered = popular.filter( (movie) => movie.genre_ids.includes(activeGenre))
    console.log(filtered)
    setFiltered(filtered)

  }, [activeGenre])

  return (
    <div className="filter-container">
      <Button
        variant={activeGenre === 0 ? 'dark' : 'outline-dark'}
        
        className='rounded-pill filter-button'
        onClick={() => setActiveGenre(0)} >
          {/* button name */}
        All
      </Button>


      <Button 
        variant={activeGenre === 35 ? 'dark' : 'outline-dark'}
        className='rounded-pill filter-button'
        onClick={() => setActiveGenre(35)}>
           {/* button name */}
        Comedy
      </Button>


      <Button
        variant={activeGenre === 28 ? 'dark' : 'outline-dark'}
        className='rounded-pill filter-button'
        onClick={() => setActiveGenre(28)}>
           {/* button name */}
        Action
      </Button>

    </div>
  )

}

export default Filter