import React from 'react'
import {motion} from 'framer-motion'
function Movie({movie}) {
  return (
    <motion.div
    layout
    whileHover={{scale: 1.1}}
    whileTap={{scale: 0.9}}
    >
        <h2>
            {movie.title}
        </h2>
        <img src = {`https://image.tmdb.org/t/p/w500${movie.backdrop_path}`} alt="" />
    </motion.div>
  )
}

export default Movie